/* Estilos generales para Servicios */
.servicios {
  margin-top: 60px;
  padding: 40px 0;
  background-color: #f9f9f9;
  
  max-width: 1200px;
  padding: 0 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px; /* Espacio entre los contenedores */
}

.servicios h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
  margin-top: 60px;
  width: 100%; /* Asegura que el título ocupe toda la línea */
}

/* Estilos para cada servicio */
.servicio {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  padding: 25px;
  margin: 0; /* Elimina márgenes para un mejor espaciado con flexbox */
  transition: all 0.3s ease;
  flex: 1 1 calc(33.333% - 20px); /* 3 elementos por renglón, ajusta según el número de columnas */
  box-sizing: border-box; /* Incluye padding y border en el cálculo del ancho */
}

.servicio:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.servicio h2 {
  color: #2c3e50;
  margin-bottom: 15px;
}

.servicio p {
  color: #555;
  margin-bottom: 20px;
  line-height: 1.6;
}

.servicio h3 {
  color: #2c3e50;
  margin-top: 20px;
  margin-bottom: 10px;
}

.servicio ul {
  padding-left: 20px;
  color: #555;
}

.servicio li {
  margin-bottom: 5px;
}

/* Estilos adicionales para servicios destacados */
.servicio.servicio-0 {
  border: 2px solid #3498db;
  position: relative;
  overflow: hidden;
}

.servicio.servicio-0::before {
  content: '🌟 Destacado';
  position: absolute;
  top: 10px;
  right: -30px;
  background: #3498db;
  color: white;
  padding: 5px 30px;
  transform: rotate(45deg);
  font-size: 0.8em;
}

/* Estilos personalizados para el servicio de IA */
.servicio.servicio-2 {
  background: linear-gradient(145deg, #ffffff, #e6f7ff);
}

.servicio.servicio-2 h2 {
  color: #0056b3;
}

/* Animación para fade in */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.servicio {
  animation: fadeInUp 0.5s ease-out;
}

/* Consultas de medios para diseño móvil */
@media (max-width: 768px) {
  .servicio {
    flex: 1 1 100%; /* Hacer que cada contenedor ocupe toda la línea en pantallas pequeñas */
  }
}
